@mixin small {
    @media (max-width: 699px) {
        @content;
    }
}

@mixin medium {
    @media (min-width: 700px) {
        @content;
    }
}

@mixin medium-large {
    @media (min-width: 900px) {
        @content;
    }
}

@mixin large {
    @media (min-width: 1200px) {
        @content;
    }
}

@mixin extra-large {
    @media (min-width: 1800px) {
        @content;
    }
}

@mixin darken($color) {
    background-color: $color;

    &:hover {
        background-color: darken($color, 20);
    }
}